import { Component, ReactNode } from "react";

import  CMap, { CZMapTP }  from 'czmap';

type Props = {
    options: CZMapTP
}
export class CZMap extends Component<Props> {
    
    componentDidMount(){
        new CMap(this.props.options)
    }

    render(): ReactNode {
        return (<div id="baidumap" style={{width: '100%', height: '560px'}} ></div>)
    }
}