
// 地图路径转换 后台数据转对应数据
export function pathChangeHandle(type:string, path:{string:any}[]):[]{
    let pathList:any = [];
    if (type === 'BMAP'){
      pathList = path.map((it:any) => ({'lng': it.longitude, 'lat': it.latitude}))
    } else if (type === 'AMAP'){
      pathList = path.map((it:any) => {
        const point = it.location.split(',')
        return {'lng':point[0], 'lat':point[1]}
      })
    } else if(type === 'TMAP'){
     console.log("开发中...");
    }
    return pathList
  }
// 围栏数据转换
/**
 * 高德：
圆形围栏数据：
{"center":"116.344132,40.061937","radius":5000}
多边形围栏数据：
{"points":"116.341622,40.063412;116.346729,40.064546;116.347072,40.060358;116.342373,40.059553"}
线形围栏数据：
{"bufferradius":200,"points":"116.347115,40.060325;116.342909,40.05957;116.336493,40.058338"}
行政区围栏：
{"adcode":110105}

 * 百度：
圆形围栏数据：
{"latitude":"40.061937","radius":5000,"longitude":"116.344132"}
多边形围栏数据：
{"vertexes":"40.063412,116.341622;40.064546,116.346729;40.060358,116.347072;40.059553,116.342373"}
线形围栏数据：
{"offset":200,"vertexes":"40.060325,116.347115;40.05957,116.342909;40.058338,116.336493"}
行政区围栏：
{"keyword":"北京市"}
 * **/
export function fenceDataChange(type:string,shape:string,data:any){
  let fenceData:any = {}
  // 高德地图 圆形围栏
  if (type === 'AMAP' && shape === 'CIRCLE'){
    fenceData.point = data.center.split(',')
    fenceData.radius = data.radius
    fenceData.type = 'circle'
  // 高德地图 多边形围栏
  } else if ((type === 'AMAP' && shape === 'POLYGON') ){
    fenceData.type = 'polygon'
    const dataArray = data.points.split(';')
    fenceData.points = dataArray.length > 0 && dataArray.map((it:string) => {
      const point = it.split(',')
      return {lng:point[0], lat: point[1]}
    })
    // 高德地图 线性形围栏
  } else if(type === 'AMAP' && shape === 'POLYLINE'){
    fenceData.type = 'polyline'
    const dataArray = data.points.split(';')
    fenceData.offset = data.bufferradius
    fenceData.points = dataArray.length > 0 && dataArray.map((it:string) => {
      const point = it.split(',')
      return {lng:point[0], lat: point[1]}
    })
    // 高德地图 行政区域围栏
  } else if(type === 'AMAP' && shape === 'DISTRICT'){
    fenceData.type = 'district'
    fenceData.adcodes = [data.adcode]
    // 百度地图 圆形围栏
  } else if (type === 'BMAP' && shape === 'CIRCLE'){
    fenceData.point = [data.longitude, data.latitude]
    fenceData.radius = data.radius
    fenceData.type = 'circle'
    // 百度地图 多边形围栏
  } else if ((type === 'BMAP' && shape === 'POLYGON')){
    fenceData.type = 'polygon'
    const dataArray = data.vertexes.split(';')
    fenceData.points = dataArray.length > 0 && dataArray.map((it:string) => {
      const point = it.split(',')
      return {lng:point[1], lat: point[0]}
    })
    // 百度地图 线性形围栏
  } else if (type === 'BMAP' && shape === 'POLYLINE'){
    fenceData.type = 'polyline'
    fenceData.offset = data.offset
    const dataArray = data.vertexes.split(';')
    fenceData.points = dataArray.length > 0 && dataArray.map((it:string) => {
      const point = it.split(',')
      return {lng:point[1], lat: point[0]}
    })
    // 百度地图 行政区域形围栏
  } else if (type === 'BMAP' && shape === 'DISTRICT'){
    fenceData.type = 'district'
    fenceData.proname = data.keyword
  }

  return fenceData
}
// 安毫秒转换为时、（h）、分(m)、秒(s)
export function timeFormat(data:string | number):string{
    let time:string = '--';
    if (data && data < 60000){
      time = Number(data)/1000 + 's'
    }  
    if (data && data >= 60000 && data < 3600000) {
      time = Number(data)/60000 + 'm'
    }
    if (data && data > 3600000) {
      time = Number(data)/3600000 + 'H'
    }
    return time;
}