import { Search, searchConfType } from "../../components/Search";
import { CZMap } from "./../../components/React-CMap";
import { Breadcrumb } from "tdesign-react";
import { useState, useEffect } from "react";
import { Table, Dialog, Button, MessagePlugin } from "tdesign-react";
import { ErrorCircleFilledIcon } from "tdesign-icons-react";
import NoData from "../../components/NoData";
import { fenceDataChange } from "../../utils/tool";
import { apiTerminal, apiDeleteTerminalld } from "../../api/api";
import moment from "moment";
const { BreadcrumbItem } = Breadcrumb;

function TrajectoryDetails(props: any) {
  // 搜索数据定义
  const serverId = props.match.params.id;
  const listData = props.location.state;
  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(10);
  const [terminalData, setTerminalData] = useState([]);
  const [total, setTotal] = useState(30);
  const [visible, setVisible] = useState(false);
  const [deleteDish, setDeleteDish] = useState({});
  const [provider] = useState("BAIDU");
  const type = listData.provider === "BAIDU" ? "BMAP" : listData.provider;
  const pointData = JSON.parse(listData.param);
  const searchConf: searchConfType[] = [
    {
      label: "设备名称:",
      key: "terminalName",
      value: "",
      callback: searchHandle,
    },
    {
      label: "设备ID:",
      key: "terminalId",
      value: "",
      callback: searchHandle,
    },
  ];
  // 围栏类型 // CIRCLE-圆形，POLYGON-多边形，POLYLINE-线性，DISTRICT-行政区
  const enclosureData = fenceDataChange(type, listData.type, pointData);
  let enclosure = {
    strokeColor: "#6699FF",
    strokeWeight: 2,
    strokeOpacity: 1,
  };
  enclosure = Object.assign(enclosure, enclosureData);
  const options = {
    id: "baidumap", // 配置地图的着陆点  id
    mapAk: "", // 写入Ak
    poiText: true, //是否显示POI信息
    poiIcon: true, //是否显示POI图标信息
    type, // 设置使用地图的类型 BMAP 百度地图、 TMAP 腾讯、、AMAP 高德
    points: [116.404, 39.915], // 中心点设置
    areaName: "上海市", // 中心城市
    zoom: 11, // 缩放级别
    enableScrollWheel: false, // 开启滚轮
    isScaleCtrl: true, // 添加比例尺控件
    isZoomCtrl: true, // 添加缩放控件
    navi3DCtrl: false, // 添加3D控件
    enclosure,
  };
  // 生命周期调用数据
  useEffect(() => {
    getData();
  }, []);
  // 获取数据
  function getData(items?: any) {
    apiTerminal({
      page: items?.page ?? page,
      pageSize: items?.pageSize ?? pageSize,
      terminalName: items?.terminalName,
      terminalId: items?.terminalId,
      provider: items?.provider ?? provider,
      serverId,
    }).then((res: any) => {
      const { data } = res;
      if (res.code === 0) {
        setTerminalData(data.items);
        setTotal(data.total);
      } else {
        MessagePlugin.error("请求出错！");
      }
    });
  }

  // 搜索功能
  function searchHandle(searchVal: any) {
    setPage(1);
    setPageSize(10);
    getData({ setTerminalPage: 1, setTerminalPageSize: 10, ...searchVal });
  }

  // 删除功能
  const deleteHandle = () => {
    if (deleteDish && (deleteDish as any).terminalId) {
      const {terminalId,serverId} = deleteDish as any
      apiDeleteTerminalld({id:terminalId,serverId, provider}).then(res => {
        if ((res as any).code == 0){
          setDeleteDish({});
          setVisible(false);
          MessagePlugin.success('信息删除成功！');
          getData()
        } else {
          MessagePlugin.error((res as any).msg);
        }
      })   
      
    } else {
      MessagePlugin.error("删除请求出错了");
    }
  };
  // 关闭弹框
  const handleFooterClose = () => {
    setVisible(false);
  };
  
  return (
    <>
      <div className="BreadcrumbCont">
        <Breadcrumb maxItemWidth="200px" theme="light">
          <BreadcrumbItem href="/fence">电子围栏</BreadcrumbItem>
          <BreadcrumbItem>详情</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="container trajectory_details">
        <div className="detailCont">
          <Table
            data={[listData]}
            columns={[
              {
                align: "left",
                className: "test",
                ellipsis: true,
                colKey: "name",
                title: "围栏名称",
                minWidth: 200,
              },
              {
                align: "left",
                className: "test4",
                ellipsis: true,
                colKey: "fenceId",
                title: "围栏ID",
                minWidth: 200,
              },
              {
                align: "left",
                className: "test4",
                ellipsis: true,
                cell({ row }) {
                  switch (row?.type) {
                    case "DISTRICT":
                      return <>行政区域</>;
                    case "CIRCLE":
                      return <>圆形</>;
                    case "POLYGON":
                      return <>多边型</>;
                    case "POLYLINE":
                      return <>线形</>;
                    default:
                      return null;
                  }
                },
                title: "围栏类型",
                minWidth: 200,
              },
              {
                align: "left",
                className: "test4",
                ellipsis: true,
                cell({ row }) {
                  switch (row?.provider) {
                    case "BAIDU":
                      return <>百度地图</>;
                    case "AMAP":
                      return <>高德地图</>;
                    case "QQ":
                      return <>腾讯地图</>;
                    default:
                      return null;
                  }
                },
                title: "服务商",
                minWidth: 200,
              },
              {
                align: "left",
                className: "test4",
                ellipsis: true,
                colKey: "serverId",
                title: "服务ID",
                minWidth: 200,
              },
              {
                align: "left",
                className: "test4",
                ellipsis: true,
                colKey: "created",
                title: "创建时间",
                cell({ row }) {
                  return row.created ? moment(row.created).format("YYYY-MM-DD HH:mm:ss") : '--';
                },
                minWidth: 200,
              },
              {
                align: "left",
                className: "test4",
                ellipsis: true,
                colKey: "updated",
                title: "更新时间",
                cell({ row }) {
                  return row.updated ? moment(row.updated).format("YYYY-MM-DD HH:mm:ss") : '--';
                },
                minWidth: 200,
              },
            ]}
            rowKey="index"
            verticalAlign="top"
            bordered
            hover
          ></Table>
          <div className="DetBMap">
            <CZMap options={options} />
          </div>
        </div>
        <div className="searchCont">
          <Search searchConf={searchConf}></Search>
          <div className="tdesign-demo-block-row"></div>
        </div>
        <div className="tableCont">
          {terminalData && terminalData.length > 0 ? (
            <Table
              data={terminalData}
              columns={[
                {
                  align: "left",
                  className: "test",
                  ellipsis: true,
                  colKey: "name",
                  title: "设备名称",
                },
                {
                  align: "left",
                  className: "test",
                  ellipsis: true,
                  colKey: "provider",
                  cell({ row }) {
                    switch (row?.provider) {
                      case "BAIDU":
                        return <>百度地图</>;
                      case "AMAP":
                        return <>高德地图</>;
                      case "QQ":
                        return <>腾讯地图</>;
                      default:
                        return null;
                    }
                  },
                  title: "服务商",
                },
                {
                  align: "left",
                  className: "test4",
                  ellipsis: true,
                  colKey: "id",
                  title: "服务ID",
                },
                {
                  align: "left",
                  className: "test4",
                  ellipsis: true,
                  colKey: "created",
                  cell({ row }) {
                    return row.created ? moment(row.created).format("YYYY-MM-DD HH:mm:ss") : '--';
                  },
                  title: "创建时间",
                },
                {
                  align: "left",
                  className: "test4",
                  ellipsis: true,
                  colKey: "updated",
                  cell({ row }) {
                    return row.updated ? moment(row.updated).format("YYYY-MM-DD HH:mm:ss") : '--';
                  },
                  title: "更新时间",
                },
                {
                  align: "left",
                  className: "test4",
                  ellipsis: true,
                  title: "范围",
                  cell({ row }) {
                    switch (row?.isOut) {
                      case true:
                        return <p className="succ">未超出</p>;
                      case false:
                        return <p className="fail">已超出</p>;
                      default:
                        return <>--</>;
                    }
                  },
                },
                {
                  align: "left",
                  className: "test4",
                  ellipsis: true,
                  colKey: "desc",
                  title: "描述",
                },
                {
                  colKey: "op",
                  width: 100,
                  title: "操作",
                  align: "center",
                  fixed: "right",
                  cell(record: any) {
                    return (
                      <>
                        <a
                          className="link"
                          onClick={() => {
                            setDeleteDish(record.row);
                            setVisible(true);
                          }}
                        >
                          删除
                        </a>
                      </>
                    );
                  },
                },
              ]}
              rowKey="index"
              verticalAlign="top"
              bordered
              hover
              pagination={{
                defaultCurrent: page,
                defaultPageSize: pageSize,
                total,
                showJumper: true,
                onChange(pageInfo) {
                  setPageSize(pageInfo.pageSize);
                  setPage(pageInfo.current);
                  getData();
                },
              }}
            ></Table>
          ) : (
            <NoData />
          )}
          <Dialog
            header={
              <>
                <ErrorCircleFilledIcon style={{ color: "#ED7B2F" }} />
                <span>确定要删除这条轨迹吗？</span>
              </>
            }
            footer={
              <>
                <Button
                  theme="default"
                  variant="outline"
                  onClick={() => setVisible(false)}
                >
                  取消
                </Button>
                <Button theme="danger" onClick={() => deleteHandle()}>
                  删除
                </Button>
              </>
            }
            visible={visible}
            onClose={handleFooterClose}
          ></Dialog>
        </div>
      </div>
    </>
  );
}

export default TrajectoryDetails;
